import React from 'react';
import Extension from './Extension';
import { ContentType } from '../../lib/enum';
import { Config } from '../../lib/util';
import AutoComplete from '../../lib/autocomplete';
import Group from '../sidebar/Property/Group';
import { AutoCompletePanel } from '../../components';
import { Link, Colors, Align, LineHeight, BorderRadius, Color, Space, Line } from '../sidebar/Property/items';
import './lib/tinymce.min.js';


class Button extends Extension {

  state = {
    showDynamic: false,
    query: '',
    data: [],
    position: { x: 0, y: 0 },
  }

  getIconClass() {
    return 'icon icon-button';
  }

  getContentType() {
    return ContentType.BUTTON;
  }

  getLabel() {
    return 'Button';
  }

  componentDidMount() {
    this.autoComplete = new AutoComplete();
  }

  toHtml(data) {
    const { text, color, link, linkType, padding, backgroundColor,
      containerPadding, hoverColor, hoverBackgroundColor, textAlign, lineHeight, borderRadius,
      lineColor, lineWidth, lineStyle, _meta } = data;

    const node = document.createElement('div');
    node.innerHTML = text;
    [].slice.call(node.querySelectorAll('p')).forEach(element => (element.style.display = 'inline'));
    const html = node.innerHTML;
    return `<div>
      <div style="text-align:${textAlign};padding:${containerPadding}">
      <style>
        #button_${_meta.guid}:hover{
          color:${hoverColor} !important;
          background-color:${hoverBackgroundColor} !important;
        }
      </style>
      <a target="${linkType}" href="${link || 'javascript:void(0)'}" id="button_${_meta.guid}"
        style="display:inline-block;text-decoration: none;cursor: ${link ? 'pointer' : 'default' };color:${color};background-color:${backgroundColor};padding:${padding};line-height:${lineHeight}%;border-radius:${borderRadius}px;border:${lineWidth}px ${lineStyle} ${lineColor};">${html}</a>
      </div>
    </div>`;
  }

  getInitialAttribute() {
    return {
      linkType: '_self',
      text: 'Text Button',
      link: '',
      color: '#fff',
      padding: '10px 20px 10px 20px',
      backgroundColor: '#3aaee0',
      hoverColor: '#FFF',
      hoverBackgroundColor: '#2a92bf',
      textAlign: 'center',
      lineHeight: 120,
      borderRadius: 4,
      containerPadding: '10px',
      lineStyle: 'solid',
      lineWidth: 0,
      lineColor: '#3aaee0',
    };
  }

  getProperties(values, update) {
    const { color, linkType, link, backgroundColor, hoverColor, hoverBackgroundColor,
      containerPadding, padding, textAlign, lineHeight, borderRadius,
      lineStyle, lineWidth, lineColor } = values;
    return <React.Fragment>
      <Group title="LINK">
        <Link link={link} linkType={linkType} title="Button Link" onUpdate={update} />
      </Group>
      <Group title="COLORS">
        <Colors title="Colors" colors={{
          color,
          backgroundColor,
          hoverColor,
          hoverBackgroundColor
        }} onUpdate={update} />
      </Group>
      <Group title="SPACING">
        <Align align={textAlign} onUpdate={update} />
        <LineHeight lineHeight={lineHeight} onUpdate={update} />
        <Line title="Border" lineWidth={lineWidth} lineStyle={lineStyle} lineColor={lineColor} onUpdate={update} />
        <BorderRadius borderRadius={borderRadius} onUpdate={update} />
        <Space title="Padding" value={padding} attribute="padding" onUpdate={update} />
      </Group>
      <Group title="GENERAL">
        <Space title="Container Padding" value={containerPadding} attribute="containerPadding" onUpdate={update} />
      </Group>
    </React.Fragment>
  }

  
  initEditor = () => {
    tinymce.init({
      target: this.dom,
      menubar: false,
      toolbar: ['fontselect fontsizeselect | bold italic underline'],
      inline: true,
      setup: (ed) => {
        this.editor = ed;
        this.onRef(ed);
        ed.on('keydown', (e) => {
          if (e.keyCode === 13) {
            e.preventDefault();
          }
        });
      }
    });
  }


  onRef = (editor) => {
    if (editor && this.autoComplete) {
      this.autoComplete.on(editor, /^.*#([^#]*)$/, (result) => {
        if (result.match) {
          this.setState({
            showDynamic: true,
            position: result.position,
            query: result.query,
            data: Config.get('mentions').filter(item => item.key.indexOf(result.query) !== -1)
          });
        } else {
          this.setState({ showDynamic: false, query: '' });
        }
      });
    }
  }

  handleEditorChange = (value) => {
    const { onUpdate } = this.props;
    let content = value.target.getContent({ format: 'raw' });
    var regex = /(<([^>]+)>)/ig;
    const pureContent = content.replace(regex, "");
    onUpdate('text', pureContent ? content : "");
  }

  insertDynamic = (value) => {
    if (this.editor) {
      Array(this.state.query.length + 1).fill().forEach(i => {
        this.editor.execCommand('delete');
      });
      this.editor.insertContent('[[' + value.key + ']]', { merge: true });
      this.setState({ showDynamic: false, query: '' });
    }
  }

  //WARNING! To be deprecated in React v17. Use new lifecycle static getDerivedStateFromProps instead.
  componentWillReceiveProps({ textAlign, lineHeight, color, focus, _meta }) {

    if (!focus) {
      this.autoComplete.off();
      this.editor && this.handleEditorChange({ target: this.editor });
      this.editor && this.editor.remove(this.dom);
      this.editor = null;
    } else {
      !this.editor && this.initEditor();
    }
  }

  componentWillUnmount() {
    super.componentWillUnmount();
    if (this.autoComplete) {
      this.autoComplete.off();
    }
  }

  render() {
    const { focus, text, color, padding, backgroundColor, containerPadding,
      hoverColor, textAlign, lineHeight, borderRadius,
      lineStyle, lineWidth, lineColor } = this.props;
      console.log(text);
    return <div className="ds_content_button">
      <div style={{
        textAlign: textAlign,
        padding: containerPadding,
      }}>
        <a className="mce-content-wrapper"
          style={{
            color,
            backgroundColor,
            padding,
            lineHeight: lineHeight + '%',
            borderRadius: borderRadius + 'px',
            border: `${lineWidth}px ${lineStyle} ${lineColor}`
          }}
        >
          <p ref={(dom) => {this.dom = dom;}} dangerouslySetInnerHTML={{ __html: text }}></p>
        </a>
      </div>
      <AutoCompletePanel
        data={this.state.data}
        show={this.state.showDynamic}
        position={this.state.position}
        onClick={(item) => { this.insertDynamic(item); }}
        onClose={() => { this.setState({ showDynamic: false, query: '' }) }}
      />
    </div>;
  }
}

export default Button;